<script>
export default {
  name: "constructionManagementPlatformComponent",
  components: {},
  data() {
    return {
      south: [],
      north: [],
    };
  },
  computed: {},
  methods: {
    // 進去廠區
    intoFactory(item) {
      if (item !== "south" && item !== "north") {
        this.$router.push({
          name: "constructionStatistics",
          params: { detail: JSON.stringify(item) },
        });
      } else {
        const obj = {
          description: item === "north" ? "北廠區" : "南廠區",
          e_Description: item,
          location_Id: item,
        };
        this.$router.push({
          name: "constructionStatistics",
          params: { detail: JSON.stringify(obj) },
        });
      }
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.$store.dispatch("constructionMgmQryFacs").then((res) => {
      const data = res.data.data;
      this.north = data.north;
      this.south = data.south;
    });
  },
};
</script>

<template src="./constructionManagementPlatform.html"></template>
<style lang="sass" scoped src="./constructionManagementPlatform.sass"></style>
